.wrapper {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1,1fr);
    grid-gap: 15px;
    grid-auto-rows: 100px;
    grid-template-areas:
            "a"
            "a"
            "a"
            "a"
            "c";
}


.item1 {
    grid-area: a;
}

.item2 {
    font-family: 'Amaranth', sans-serif;
    font-size: 2em;
    color: white;
    margin-top:3px;
    grid-area: a;
}

.item3 {
    grid-area: c;
    align-self: start;
    font-family: 'Roboto', sans-serif;
    font-size: 2vh;
    color:white;
    line-height: 1.6;
}
